import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';  
import classnames from 'classnames';
import { Modal, Button } from 'antd';
import './viprecord.less'; 
import CommonKeyboardNum from 'components/common-keyboard-num';//键盘
import PromptPopup from 'components/prompt-popup';

import SelectWaiterPopup from '../select-waiter-popup';
import WechatPay from 'components/wechat-pay-popup';


function VipRecord({detail}){
	return 	<div className="vip-record">
			<div className="vip-icon">
				<i className="iconfont icon-huiyuandangan_icon_user-"></i>
			</div>
			<div className="vip-block">
				<div><span className="bl-title">姓名：</span>{detail.memberName}</div>
				<div className="block-top"><span className="bl-title">电话：</span>{detail.mobile}</div> 
			</div>
			<div className="vip-block">
				<div><span className="bl-title fen-ge">卡号：</span>{detail.cardID}</div>
				<div className="block-top"><span className="bl-title fen-ge">账户余额：</span>{detail.accountBalance}</div>
				<div className="block-top"><span className="bl-title fen-ge">累计充值：</span>{detail.cumulateRecharge}</div>
			</div>
			<div className="vip-block">
				<div><span className="bl-title fen-ge">等级：</span>{detail.cardLevelName}</div>
				<div className="block-top"><span className="bl-title fen-ge">账户积分：</span>{detail.cardCurrentBonus}</div>
				<div className="block-top"><span className="bl-title fen-ge">累计消费：</span>{detail.cumulateConsume}</div>
			</div>
		</div> 
}

@inject('memberStore') @observer
class Viprecord extends React.Component {
  
	constructor(props){
		super(props);
		this.state={
			result:'',
			countermanPopup:'',
			signInvoice:false,
			currentIndex:0,
			currentText:'现金',
			confirmPopup:''
		};
		this.props.memberStore.initialVip();
	}

	handleCancel=()=>{
		this.props.memberStore.initialVip();
	 	
		this.props.memberStore.cancelVipClick();
	} 

	handleOk=()=>{
		this.props.memberStore.showVipConfirm();
		
	}  
  
  render() {
		 let memberStore=this.props.memberStore;

		 let payBlock;
		 let payStyles=["现金","微信","支付宝",'K币','银联卡','信用卡'];
		 payBlock=payStyles.map((pay,index)=>{
			 return <button onClick={()=>{
				 this.setState({currentIndex:index});
				 this.setState({currentText:pay});
			 }} key={index} className={classnames({
				 "select":index===this.state.currentIndex
			 })}>{pay}</button>
		 });

		 let confirmPopup;
		 if(memberStore.isShowVipConfirm){


			let depositType,depositName;
				switch(this.state.currentIndex){
					case 0:
					depositType=546;
					depositName="现金";
					break;
					case 1:
					depositType=543;
					depositName="微信";
					break;
					case 2:
					depositType=544;
					depositName="支付宝";
					break;
					case 3:
					depositType=540; //K币值不对
					depositName='K币';
					break;
					case 4:
					depositType=545;
					depositName='银联卡';
					break;
					case 5:
					depositType=542;
					depositName='信用卡';
					break;
					default:
					break;
				}
				let countermanID,countermanName,signInvoice;
				
				this.props.memberStore.operateWaiter.loginID?countermanID=this.props.memberStore.operateWaiter.loginID:countermanID=null;

				this.props.memberStore.operateWaiter.userName?countermanName=this.props.memberStore.operateWaiter.userName:countermanName=null;

				this.state.signInvoice?signInvoice=1:signInvoice=0;
				let recharge={
					customerID:this.props.memberStore.memberItem.customerID,
					cardID:this.props.memberStore.memberItem.cardID,
					deposit:this.state.result,
					present:this.props.memberStore.rechargeRule.presentAmount,
					fraction:this.props.memberStore.rechargeRule.presentBonus,
					depositType:depositType,
					depositName:depositName,
					authCode:'',
					countermanID:countermanID,
					countermanName:countermanName,
					ruleID:this.props.memberStore.rechargeRule.ruleID,
					signInvoice:signInvoice,
				}

			if(this.state.currentIndex===0||this.state.currentIndex===4||this.state.currentIndex===5){
					confirmPopup =<PromptPopup onCancel={()=>{
					this.props.memberStore.cancelVipConfirm();
				}} onOk={()=>{
					//"现金","微信","支付宝",'K币','银联卡','信用卡'
					
					console.log(recharge);
					this.props.memberStore.submitMemberRecharge(recharge);
				}}><div className="prompt">
					<div className="delele-text">确定对会员卡({this.props.memberStore.memberItem.cardID})充值
					<span>{this.state.result}元({this.state.currentText}）</span>吗？
					</div>
				</div>
				</PromptPopup>
		 	} else{
					confirmPopup=<WechatPay inputValue={this.state.result} onCancel={()=>{
						this.props.memberStore.cancelVipConfirm();
					}} onOk={(result)=>{
						recharge.authCode=result;
						console.log(recharge);
						this.props.memberStore.submitMemberRecharge(recharge);
					}}/>
			}
				
		 }
		 
		
    return (
      <div> 
        <Modal title="会员充值" visible={true} maskClosable={false}   
            onCancel={this.handleCancel}
            onOk={this.handleOk}  
            width={865} wrapClassName="vip-popup-modal"
        >
        <div className="modal-content">
	        <div className="modal-data">
						
					  <VipRecord detail={memberStore.memberItem}/>

						<div className="vip-conten">
							<p className='recharge-money'>
								<span>本次充值:</span>
								<em>￥<input type='text' value={this.state.result} /></em>
								<b onClick={()=>{
									this.setState({signInvoice:!this.state.signInvoice});
								}}>{this.state.signInvoice?<i className="iconfont icon-icon_checkbox_sel-"></i>:<i className="iconfont icon-icon_checkbox_dis"></i>}开发票</b>
							</p>
							<p>
								<span>赠送金额:</span>
								<em>{memberStore.rechargeRule.presentAmount}</em>
							</p>
							 <p>
								<span>赠送积分:</span>
								<em>{memberStore.rechargeRule.presentBonus}</em> 
							</p>
							 <p>
								<span>业务员:</span>
								<em>{memberStore.operateWaiter.userName}</em> 
								<i className="iconfont icon-huiyuanguanli_icon_ziliao waiter" onClick={()=>{
									 this.setState({countermanPopup:<SelectWaiterPopup cancelClick={()=>{
												this.setState({countermanPopup:''});
										}} okClick={()=>{
												this.setState({countermanPopup:''});
										}}/>});
								}}></i>
							</p>
							 <p>
								<span>结算方式:</span>
							</p> 

							<div className="btn-lis">
								{payBlock}
							</div>
							
						</div>
						<div className="kecord-num">
							<CommonKeyboardNum width={319} height={289} Whetherpoint getResult={(result)=>{
									this.setState({result:result});
									if(result>0){
										memberStore.getRechargeCalc(result);
									}
								
							}}></CommonKeyboardNum>
						</div>
		      </div>
		    	 
		    </div>
        </Modal>
				{confirmPopup}
				{this.state.countermanPopup}
      </div>
    );
  }
}

export default Viprecord;


