import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';  

import { Modal, Button } from 'antd';
import './draw_money.less';
import '../modal.css';

@inject('memberStore') @observer
class DrawMoney extends React.Component {

	constructor(props){
		super(props);
		this.state={
			rechargeAmount:this.props.memberStore.rechargeItem.rechargeValue,
			ticketNo:'',
			ticketUnit:'',
			ticketAmount:'',
			ticketMemo:'',
			errorNo:'',
			errorAmount:''
		}
	}
  
	handleCancel=()=>{
		this.state={
			rechargeAmount:'',
			ticketNo:'',
			ticketUnit:'',
			ticketAmount:'',
			ticketMemo:'',
			errorNo:'',
			errorAmount:''
		}
		this.props.memberStore.drawMoneyCancel();
	}
 
	handleOk=()=>{

		if(this.state.ticketNo){
			this.setState({errorNo:""});
		}else{
			this.setState({errorNo:'发票编号不能为空'});
		}

		if(this.state.ticketAmount){
			this.setState({errorAmount:""});
		}else{
			this.setState({errorAmount:"发票金额大于0"});
		}

		if(this.state.ticketNo&&this.state.ticketAmount){
				let submit = {
					cardID:this.props.memberStore.rechargeItem.cardID,
					depositID:this.props.memberStore.rechargeItem.depositID,
					customerID:this.props.memberStore.rechargeItem.customerID,
					rechargeAmount:this.state.rechargeAmount,
					ticketNo:this.state.ticketNo,
					ticketUnit:this.state.ticketUnit,
					ticketAmount:this.state.ticketAmount,
					ticketMemo:this.state.ticketMemo
				}
				this.props.memberStore.memberOpenTicket(submit);
		}

	}

  render() {
    return (
      <div> 
        <Modal title="开发票" visible={true} maskClosable={false}   
            onCancel={this.handleCancel}  closable={false} onOk={this.handleOk}
          width={840} wrapClassName="draw-money-popup-modal"
        >
        <div id="draw-money">
					<div className="draw-main">
						<p>
							<span>充值金额</span>
							 <em>{this.state.rechargeAmount}</em>
						</p>
						<p>
							<span>发票编号 <i>*</i></span>
							<input type="text" value={this.state.ticketNo} onChange={(e)=>{
								this.setState({ticketNo:e.target.value});
								if(e.target.value){
										this.setState({errorNo:''});					
								}else{
									this.setState({errorNo:'发票编号不能为空'});
								}
							}}/>
							<div className="error">{this.state.errorNo}</div>
						</p>
						<p>
							<span>开票单位</span>
							<input type="text" value={this.state.ticketUnit} onChange={(e)=>{
								this.setState({ticketUnit:e.target.value});
							}}/>
						</p>
						<p>
							<span>发票金额 <i>*</i></span>
							<input type="text" value={this.state.ticketAmount} onChange={(e)=>{
								this.setState({ticketAmount:e.target.value});
								if(e.target.value>0){
										this.setState({errorAmount:''});
								}else{
										this.setState({errorAmount:"发票金额大于0"});
								}
							}} />
							<div className="error">{this.state.errorAmount}</div>
						</p>
						<p>
							<span>开票备注</span>
							<textarea  value={this.state.ticketMemo} onChange={(e)=>{
								this.setState({ticketMemo:e.target.value});
							}}/>
						</p>
					</div>
		    </div>
        </Modal>
      </div>
    );
  }
}

export default DrawMoney;


